@use '@talend/design-tokens/lib/tokens' as tokens;

.navbar-inverse {
	color: tokens.$coral-color-neutral-text-inverted;
	background: tokens.$coral-color-branding-navigation;
}

.navbar-default {
	color: tokens.$coral-color-neutral-text;
	background: tokens.$coral-color-neutral-background-medium;

	.navbar-nav > li > a {
		color: tokens.$coral-color-neutral-text;
	}

	.btn-link:hover,
	.btn-link:focus,
	.navbar-nav > li > a:hover,
	.navbar-nav > li > a:focus {
		color: tokens.$coral-color-neutral-text;
		background: tokens.$coral-color-neutral-background-strong;
	}
}

.navbar {
	border: none;

	&-default,
	&-inverse {
		.navbar-brand {
			font-size: 15px;

			&::before {
				content: '';
				float: left;
				margin-right: $padding-small;
				height: $navbar-brand-logo-height;
				width: $navbar-brand-logo-width;
				background: url('data:image/svg+xml;base64,')
					no-repeat;
				background-size: 90%;
			}
		}
	}

	&-nav {
		display: flex;
		align-items: center;
	}

	a {
		font-weight: normal;
	}
}

.navbar-default .navbar-nav > .active > a,
.navbar-default .navbar-nav > .active > a:hover,
.navbar-default .navbar-nav > .active > a:focus {
	color: tokens.$coral-color-neutral-text;
	background-color: tokens.$coral-color-neutral-background-strong;
	// background-color: ;
}
